
html,
body {
  height: 100%;
}
body {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.25;
  margin: 0;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
input,
select,
textarea,
button {
  font-family: inherit;
  outline-width: 0;
}



