
.chat {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: #fff;
}
.chat:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: url("~@/assets/background.jpg");
    opacity: 0.3;
}
.chat > .chat_messages,
  .chat > .chat_form {
    padding: 1rem;
}
.chat > .chat_messages {
    overflow-y: auto;
    padding-bottom: 0;
    height: calc(100% - 85px);
}



