
.chat-form {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.25);
}
.chat-form > .chat-form_icon-pen {
    margin: 0.55rem;
    position: absolute;
    left: 1rem;
    height: 1.2rem;
    color: lightgray;
}
@media screen and (max-width: 400px) {
.chat-form > .chat-form_icon-pen {
        display: none;
}
}
.chat-form > .chat-form_message {
    position: relative;
    margin-top: -12px;
    font-size: .8rem;
    color: #fff;
    line-height: 1rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.chat-form > .chat-form_input,
  .chat-form > .chat-form_button {
    display: inline-block;
    margin: 0;
    padding: 0.5rem;
    font-size: 1rem;
    line-height: 1.25rem;
    border: 1px solid #69b1de;
    box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.25);
}
.chat-form > .chat-form_input {
    width: calc(100% - 8rem);
    color: gray;
    border-right: none;
    padding-left: 2.7rem;
}
@media screen and (max-width: 400px) {
.chat-form > .chat-form_input {
        padding-left: 0.5rem;
}
}
.chat-form > .chat-form_button {
    width: 8rem;
    border-left: none;
    background-color: #2881b9;
    color: #fff;
    border-color: #2881b9;
    -webkit-transition: .5s;
    transition: .5s;
}
.chat-form > .chat-form_button:disabled {
      background-color: #69b1de;
      border-color: #69b1de;
}
.chat-form > .chat-form_button:not([disabled]) {
      cursor: pointer;
}



