
.chat-message {
  padding: .5rem;
  margin-bottom: 1rem;
  position: relative;
  background-color: #fff;
  border-radius: .5rem;
  box-shadow: 0 0 0.4rem rgba(0, 0, 0, 0.25);
  font-size: .9rem;
  line-height: 1.2rem;
}
.chat-message > .chat-message_user {
    margin-bottom: .5rem;
    font-weight: bold;
    color: #2881b9;
}
.is-me.chat-message > .chat-message_user {
      color: red;
}
.chat-message > .chat-message_time {
    padding: .5rem;
    position: absolute;
    top: 0;
    right: 0;
    color: gray;
}
.chat-message > .chat-message_error {
    text-align: right;
    color: red;
    font-size: .75rem;
}
.chat-message:before, .chat-message:after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  top: 1.5rem;
  border: 1rem solid #fff;
  border-color: #fff transparent transparent transparent;
}
.chat-message:before {
  border-top-color: gray;
}
.chat-message.is-server {
  margin-left: 2rem;
}
.chat-message.is-server:before, .chat-message.is-server:after {
    left: -1rem;
}
.chat-message.is-server:before {
    margin-left: -1px;
}
.chat-message.is-me {
  margin-right: 2rem;
}
.chat-message.is-me:before, .chat-message.is-me:after {
    right: -1rem;
}
.chat-message.is-me:before {
    margin-right: -1px;
}



